body {
  margin: 0;
  background-color: black;
}
p {
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}
*{
    scrollbar-width: 0;
}
*::-webkit-scrollbar {
  display: none;
}
.parenthome {
  height: 100vh;
  width: 100vw;
  background-color: black;
 
  position: relative;
  overflow-y: hidden;
}
.maincontent{
  height: 100%;
  width: 100%;
  z-index: 1;
  position: absolute;
    top: 0%;
    left: 0%;
    /* transform: translate(-50%,-50%); */

    display: grid;
    grid-template-rows: 170px 1fr;

}
.titlebar {
  grid-row: 1/2;
  width: 100%;
  /* display: flex;
  justify-content: flex-start;
  align-items: center; */
  gap: 10px;
  /* background:linear-gradient(
    90deg,
    red 0%,
    rgb(172, 73, 73) 30%,
    rgb(245, 175, 175) 100%
  ); */
  background-color: black;
  position: relative;
  
}

.dot {
  animation: dotappear 0.9s ease 7.1s 1 normal forwards;
  opacity: 0;
  color: white;
  font-size: 75px;
}

.fmactitle  {
  font-size: 100px;
  /* font-family: 'Diphylleia', serif; */
  /* font-family: 'Permanent Marker', cursive; */
  font-family: "Protest Riot", sans-serif;
  /* font-family: 'Merriweather', serif; */
  font-family: 'Montserrat', sans-serif;

  position: absolute;
  left: 50%;
  top: 110%;
  white-space: nowrap;
  transform: translate(-50%,0%);
  animation: reposition 1s ease 7.5s 1 normal forwards,titletextappear 8s ease 0.1s normal forwards;
  z-index: 5;
  opacity: 0;
}
.hightlighttext {
  color: white;
  animation: colourchange 0.7s ease 6s 1 normal forwards;
}

.disappearingtext {
  color: white;
  animation: textdisappear 1.3s ease 6.2s 1 normal forwards;
}
.fmaclogo {
  height: 180px;
  width: 200px;
  object-fit: cover;
  position: absolute;
  left: 0%;
  top: 50%;
  transform: translateY(-50%);
  opacity: 0;
  animation: logoappear 0.4s ease 8.5s 1 normal forwards;
}
.date{
  position: absolute;
  top:65%;
  left: 50%;
  transform: translateX(-50%);
  display: flex;
  justify-content: center;
  align-items: center;

}
.date p{
  font-size: 20px;
  color: white;
  font-family: 'Diphylleia', serif;
  transform: scale(1);
  opacity: 0;
  animation: logoappear 0.7s ease 8.6s 1 normal forwards;
}
.contentbox {
  grid-row: 3/4;
  background-color: rgb(131, 77, 141);
  display: grid;
  grid-template-rows: 70px 1fr;
  grid-template-columns: 0px 1fr 0px;
}

.menubar {
  display: flex;
  justify-content: space-around;
  align-items: center;
  grid-column: 2/3;
  grid-row: 1/2;
  width: 100%;
  /* background-color: gray; */
  /* background:linear-gradient(
    90deg,
    red 0%,
    rgb(172, 73, 73) 30%,
    rgb(245, 175, 175) 100%
  ); */
  background-color: black;
  /* border: 0px solid white;
  border-radius: 10px;
  border-bottom: none;
  border-right: none;
  border-left: none; */
  z-index: 2;
}

.menuoption {
  height: 86%;
  width: 15%;
  background-color: black;
  border-radius: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: transform 0.2s ease, background-color 0.2s ease;
  margin-top: 5px;
  animation: newsappear 0.5s ease 8.5s 1 normal forwards;
  transform: scale(0);
}
.menuoption p {
  font-family: "Protest Riot", sans-serif;
  font-size: 20px;
  transition: color 0.2s ease;
  color: white;
  transition: font-size 0.2s ease;
}
.menuoption:hover {
  background-color: rgba(241, 62, 62, 0.549);
}
/* .menuoption:hover p{
    color: white;
} */

.news {
  grid-column: 2/3;
  grid-row: 2/3;
  background-color: black;
  display: grid;
  grid-template-columns: repeat(10,1fr);
  grid-auto-rows: minmax(100px, auto);  
  gap: 10px;
}
.loadingscreen{
    grid-column:1/11 ;
grid-row: 1/7;   
 background-color:transparent;
