.newsimage{
    height: 100%;
    width: 100%;
    object-fit: cover;
    animation: newsappear 0.6s ease-out 6s 1 normal forwards;
    transform: scale(0);
    opacity: 0.5;
    transition: transform 0.3s ease, opacity 0.3s ease;
   z-index: 3;
   background-color: black;

}

.newsbox:hover .newsimage{
opacity: 1;
}

.newstag {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    text-align: center;
    width: 70%;
    opacity: 0;
    animation: newstextappear 1s linear 6s 1 normal forwards;
    transition: top 0.3s ease, left 0.3s ease;
}
.newsbox:hover .newstag{
  opacity: 1;
  font-size: 30px;
  top:5%;
  left: 50%;
  width: 80%;
  text-align: left;

