.loader {
    border: 5px solid rgba(0, 0, 0, 0.1); /* Light grey border */
    border-radius: 50%; /* Make it round */
    border-top: 5px solid #db3434a6; /* Blue border */
    width: 70px; /* Size */
    height: 70px; /* Size */
    animation: spin 2s linear  infinite; /* Animation */
    display: none;
  }
  
  @keyframes spin {
    0% { transform: rotate(0deg); }
    100% { transform: rotate(360deg); }
  }



@keyframes textdisappear {
  0% {
    font-size: 100px;
    opacity: 1;
  }
  60% {
    font-size: 100px;
    opacity: 0;
  }

  100% {
    font-size: 0px;
    opacity: 0;
  }
}

@keyframes dotappear {
  0% {
    font-size: 75px;
    opacity: 0;
  }

  100% {
    font-size: 75px;
    opacity: 1;
  }
}

@keyframes newsappear{
    0%{
        transform: scale(0);
    }

    100%{
        transform: scale(1);
    }
}


@keyframes newstextappear{
  0%{
     opacity: 0;
  }

  100%{
      opacity: 1;
  }
}
@keyframes reposition{
  0%{
    top: 110%;
    left: 50%;
    font-size: 100px;
  }
  100%{
    top: -30%;
    left: 50%;
    font-size: 75px;
  }
}

@keyframes logoappear{
  0%{
    opacity: 0;
  }

  100%{
    opacity: 1;
  }
}

@keyframes colourchange {
  0%{
    color: white;
  }
  100%{
    color: red;
  }
  
}

@keyframes titletextappear{
  0%{
    transform: translate(-50%,0%)scale(0.8);
    opacity: 0;
  }

  100%{
    transform: translate(-50%,0%)scale(1);
    opacity: 1;
  }
}